<template>
  <div class="send-currency-container page-container">
    <div class="send-form">
      <div class="to-address">
        <div class="title">
          <h4>{{ $t('interface.sendTxToAddr') }}</h4>
        </div>
        <div class="address-block">
          <input
            ref="address"
            v-model="address"
            type="text"
            name="name"
            autocomplete="off"
          />
          <i
            :class="[
              isValidAddress && hexAddress.length !== 0 ? '' : 'not-good',
              'fa fa-check-circle good-button'
            ]"
            aria-hidden="true"
          />
          <button class="qr-btn" @click.prevent="qrReaderOpen">QR</button>
        </div>
      </div>
      <div class="amount">
        <div class="title">
          <h4>{{ $t('interface.sendTxAmount') }}</h4>
          <p
            class="title-button prevent-user-select"
            @click="sendEntireBalance"
          >
            Entire Balance
          </p>
        </div>
        <div class="amount-number">
          <input
            v-validate="'min_value:0'"
            v-model="value"
            type="number"
            placeholder="Amount"
            min="0"
            name="value"
            step="any"
            autocomplete="off"
          />
          <i
            :class="[
              !isValidAmount.valid || errors.has('value') ? 'not-good' : '',
              'fa fa-check-circle good-button'
            ]"
            aria-hidden="true"
          />
        </div>
        <div
          v-if="!isValidAmount.valid || errors.has('value')"
          class="error-message-container"
        >
          <p>{{ isValidAmount.msg }}</p>
        </div>
      </div>
    </div>

    <div
      :class="[
        validInputs ? '' : 'disabled',
        'submit-button mid-round-button-green-filled',
        'text-center'
      ]"
      @click="submitTransaction"
    >
      {{ $t('interface.sendTx') }}
    </div>
  </div>
</template>

<script>
import { Toast, Misc } from '@/helpers';
import { mapGetters } from 'vuex';
import Blockie from '@/components/Blockie';
import BigNumber from 'bignumber.js';
import ethUnit from 'ethjs-unit';
import EthTx from 'ethereumjs-tx';
import utils from 'web3-utils';

export default {
  components: {
    blockie: Blockie
  },
  props: {
    getBalance: {
      type: Function,
      default: function() {}
    }
  },
  data() {
    return {
      advancedExpand: false,
      isValidAddress: false,
      hexAddress: '',
      address: '',
      value: '0',
      data: '',
      gasLimit: '21000'
    };
  },
  computed: {
    ...mapGetters({
      gasPrice: 'gasPrice',
      account: 'account',
      walletNamespace: 'walletNamespace',
      network: 'network',
      web3: 'web3'
    }),
    balanceDefault() {
      return new BigNumber(this.account.balance);
    },
    multiWatch() {
      return (
        this.value, this.isValidAddress, this.data, new Date().getTime() / 1000
      );
    },
    txTo() {
      return this.isToken
        ? this.network.type.mobbyToken.address.toLowerCase()
        : this.hexAddress.toLowerCase();
    },
    isToken() {
      return this.walletNamespace === 'mobby';
    },
    txData() {
      if (this.isToken) {
        return this.getTokenTransferABI(
          this.value,
          this.network.type.mobbyToken.decimals
        );
      }
      return Misc.sanitizeHex(this.data);
    },
    validInputs() {
      return (
        this.isValidAmount.valid &&
        this.isValidAddress &&
        (new BigNumber(this.gasLimit).gte(0) || this.gasLimit == -1) &&
        Misc.validateHexString(this.data)
      );
    },
    isValidDecimals() {
      const decimals = (this.value + '').split('.')[1];
      if (!decimals) return true;
      if (this.isToken) {
        return decimals.length <= this.network.type.mobbyToken.decimals;
      }
      return decimals.length <= 18;
    },
    isValidData() {
      return Misc.validateHexString(this.data);
    },
    isValidGasLimit() {
      return new BigNumber(this.gasLimit).gte(0);
    },
    isValidAmount() {
      const txFee = new BigNumber(ethUnit.toWei(this.gasPrice, 'gwei')).times(
        this.gasLimit || 0
      );
      const txFeeEth = ethUnit.fromWei(txFee, 'ether');
      const notEnoughGasMsg =
        this.$t('errorsGlobal.notAValidAmountTotal') +
        ' Gas ' +
        this.$t('errorsGlobal.toSend');
      const notEnoughTokenMsg =
        this.$t('errorsGlobal.notAValidAmountTotal') +
        ' ' +
        this.network.type.mobbyToken.symbol +
        ' ' +
        this.$t('errorsGlobal.toSend');
      const notEnoughCurrencyMsg =
        this.$t('errorsGlobal.notAValidAmountTotal') +
        ' ' +
        this.network.type.currencyName +
        ' ' +
        this.$t('errorsGlobal.toSend');
      const invalidValueMsg = this.$t('errorsGlobal.invalidValue');
      const enoughTokenBalance = new BigNumber(this.value).lte(
        this.account.mobbyTokenBalance
      );
      const enoughCurrency = new BigNumber(this.value)
        .plus(txFeeEth)
        .lte(this.balanceDefault);
      const enoughGas = new BigNumber(txFeeEth).lte(this.balanceDefault);
      const validDecimal = this.isValidDecimals;
      if (new BigNumber(this.value).lt(0)) {
        return {
          msg: invalidValueMsg,
          valid: false
        };
      }
      if (this.isToken) {
        const enoughBalance = enoughTokenBalance && enoughGas && validDecimal;
        return {
          valid: enoughBalance,
          msg: enoughBalance
            ? ''
            : !enoughTokenBalance
            ? notEnoughTokenMsg
            : !enoughGas
            ? notEnoughGasMsg
            : invalidValueMsg
        };
      }
      return {
        valid: enoughCurrency && validDecimal,
        msg: enoughCurrency
          ? ''
          : !enoughCurrency
          ? notEnoughCurrencyMsg
          : invalidValueMsg
      };
    }
  },
  watch: {
    address(val) {
      this.isValidAddress = val.includes('0x') && utils.isAddress(val);
      if (this.isValidAddress) this.hexAddress = this.address;
    },
    multiWatch: utils._.debounce(function() {
      if (this.validInputs) this.estimateGas();
    }, 500)
  },
  created() {
    this.getBalance().catch(err => {
      Toast.responseHandler(err, Toast.ERROR);
    });
  },
  methods: {
    sendEntireBalance() {
      switch (this.walletNamespace) {
        case 'mobby':
          this.value = this.account.mobbyTokenBalance;
          break;
        case 'eth':
          this.value =
            this.balanceDefault > 0
              ? this.balanceDefault.minus(
                  ethUnit.fromWei(
                    new BigNumber(ethUnit.toWei(this.gasPrice, 'gwei'))
                      .times(this.gasLimit)
                      .toString(),
                    'ether'
                  )
                )
              : 0;
          break;
        default:
          break;
      }
    },
    getTokenTransferABI(amount, decimals) {
      const jsonInterface = [
        {
          constant: false,
          inputs: [
            { name: '_to', type: 'address' },
            { name: '_amount', type: 'uint256' }
          ],
          name: 'transfer',
          outputs: [{ name: '', type: 'bool' }],
          payable: false,
          stateMutability: 'nonpayable',
          type: 'function'
        }
      ];
      const contract = new this.web3.eth.Contract(jsonInterface);
      return contract.methods
        .transfer(
          this.hexAddress.toLowerCase(),
          new BigNumber(amount).times(new BigNumber(10).pow(decimals)).toFixed()
        )
        .encodeABI();
    },
    async estimateGas() {
      const coinbase = await this.web3.eth.getCoinbase();
      const params = {
        from: coinbase,
        value: this.txValue,
        to: this.txTo,
        gasPrice: Misc.sanitizeHex(
          ethUnit.toWei(this.gasPrice, 'gwei').toString(16)
        ),
        data: this.txData
      };
      this.web3.eth
        .estimateGas(params)
        .then(gasLimit => {
          this.gasLimit = gasLimit;
        })
        .catch(err => {
          this.gasLimit = -1;
          Toast.responseHandler(err, Toast.ERROR);
        });
    },
    async submitTransaction() {
      try {
        const coinbase = await this.web3.eth.getCoinbase();
        const nonce = await this.web3.eth.getTransactionCount(coinbase);
        const _tx = new EthTx({
          nonce: Misc.sanitizeHex(new BigNumber(nonce).toString(16)),
          gasPrice: Misc.sanitizeHex(
            ethUnit.toWei(this.gasPrice, 'gwei').toString(16)
          ),
          gasLimit: Misc.sanitizeHex(new BigNumber(this.gasLimit).toString(16)),
          to: this.txTo,
          value: this.txValue,
          data: this.txData,
          chainId: this.network.type.chainID
        });
        const json = _tx.toJSON(true);
        json.from = coinbase;
        this.web3.eth.sendTransaction(json).catch(err => {
          Toast.responseHandler(err, Toast.ERROR);
        });
      } catch (e) {
        Toast.responseHandler(e, Toast.ERROR);
      }
    },
    qrReaderOpen() {
      const isBarcodeScanner =
        window.cordova &&
        window.cordova.plugins &&
        window.cordova.plugins.barcodeScanner;

      if (isBarcodeScanner) {
        window.cordova.plugins.barcodeScanner.scan(
          function(result) {
            this.address = result.text;
          }.bind(this),
          function(error) {
            alert('Scanning failed: ' + error);
          },
          {
            preferFrontCamera: false, // iOS and Android
            showFlipCameraButton: false, // iOS and Android
            showTorchButton: false, // iOS and Android
            torchOn: false, // Android, launch with the torch switched on (if available)
            saveHistory: false, // Android, save scan history (default false)
            prompt: '', // Android
            resultDisplayDuration: 0, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
            formats: 'QR_CODE', // default: all but PDF_417 and RSS_EXPANDED
            orientation: 'portrait', // Android only (portrait|landscape), default unset so it rotates with the device
            disableAnimations: false, // iOS
            disableSuccessBeep: true // iOS and Android
          }
        );
      }
    }
  }
};
</script>

<style lang="scss" scoped>
@import 'SendCurrencyContainer.scss';
</style>
